import { motion } from "../../node_modules/framer-motion/dist/framer-motion";

export default function Agent() {
  return (
    <motion.div
      className="home"
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      exit={{ opacity: 0 }}
    >
      <p>Agent</p>
    </motion.div>
  );
}
